@font-face {
  font-family: 'Spoqa Han Sans';
  font-weight: 700;
  font-display: swap;
  src: local('Spoqa Han Sans Bold'),
    url(/static/media/SpoqaHanSansBold.76b8c78a.woff2) format('woff2'),
    url(/static/media/SpoqaHanSansBold.4ad74ffe.woff) format('woff'),
    url(/static/media/SpoqaHanSansBold.f6e14af6.ttf) format('truetype');
}

@font-face {
  font-family: 'Spoqa Han Sans';
  font-weight: 400;
  font-display: swap;
  src: local('Spoqa Han Sans Regular'),
    url(/static/media/SpoqaHanSansRegular.544112e5.woff2) format('woff2'),
    url(/static/media/SpoqaHanSansRegular.5ffec773.woff) format('woff'),
